import React from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import cogoToast from 'cogo-toast';
import {
  useMediaQuery,
  Grid,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
  Avatar,
  TextField,
  Button,
} from '@material-ui/core';
import { SectionHeader } from 'components/molecules';
import { contact } from '../../../../views/IndexView/data/index';

const useStyles = makeStyles(theme => ({
  list: {
    display: 'flex',
    flexDirection: 'column',
    [theme.breakpoints.up('md')]: {
      flexDirection: 'row',
    },
  },
  listItemText: {
    display: 'flex',
    flexDirection: 'column',
    flex: '0 0 auto',
  },
  listItem: {
    justifyContent: 'flex-start',
    [theme.breakpoints.up('md')]: {
      justifyContent: 'center',
    },
  },
  icon: {
    background: 'transparent',
    borderRadius: 0,
  },
  form: {
    maxWidth: 550,
    margin: `0 auto`,
    marginTop: theme.spacing(5),
    [theme.breakpoints.up('md')]: {
      marginTop: theme.spacing(10),
    },
    '& .MuiTextField-root': {
      background: theme.palette.background.paper,
    },
    '& .MuiOutlinedInput-input': {
      background: theme.palette.background.paper,
    },
    '& .MuiOutlinedInput-notchedOutline': {
      boxShadow: 'inset 0 1px 0 0 rgba(255, 255, 255, 0.1)',
      border: 'solid 1px rgba(0, 0, 0, 0.2)',
    },
  },
  inputTitle: {
    fontWeight: 700,
    marginBottom: theme.spacing(1),
  },
}));

const encode = data => {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&');
};

const validationSchema = yup.object({
  name: yup.string('Jmeno').required('Vyplte jmno'),
  email: yup
    .string('Email')
    .email('Napite email ve sprvnm tvaru')
    .required('Vyplte email'),
  zprava: yup.string('Enter your email').required('Napi zprvu'),
});

const Contact = props => {
  const { className, ...rest } = props;
  const classes = useStyles();

  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      zprava: '',
    },
    validationSchema: validationSchema,
    onSubmit: values => {
      fetch('/', {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: encode({
          'form-name': 'KontaktniFormular',
          ...values,
        }),
      })
        .then(() => {
          cogoToast.success('Zprva byla odeslna', {
            position: 'top-right',
          });
          formik.resetForm();
        })
        .catch(error => cogoToast.alert(error, { position: 'top-right' }));
    },
  });

  return (
    <div className={className} {...rest}>
      <List disablePadding className={classes.list}>
        <ListItem
          disableGutters
          data-aos="fade-up"
          className={classes.listItem}
        >
          <ListItemAvatar>
            <Avatar
              src="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-phone.png"
              srcSet="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-phone@2x.png 2x"
              className={classes.icon}
            />
          </ListItemAvatar>
          <a href={`tel:${contact.mobile}`}>
            <ListItemText
              className={classes.listItemText}
              primary="Telefon"
              secondary={contact.mobile}
              primaryTypographyProps={{
                variant: 'subtitle1',
                color: 'textSecondary',
              }}
              secondaryTypographyProps={{
                variant: 'subtitle1',
                color: 'textPrimary',
                component: 'span',
              }}
            />
          </a>
        </ListItem>

        <ListItem
          disableGutters
          data-aos="fade-up"
          className={classes.listItem}
        >
          <ListItemAvatar>
            <Avatar
              src="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-mail.png"
              srcSet="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-mail@2x.png 2x"
              className={classes.icon}
            />
          </ListItemAvatar>
          <a href={`mailto:${contact.email}`}>
            <ListItemText
              className={classes.listItemText}
              primary="Email"
              secondary={contact.email}
              primaryTypographyProps={{
                variant: 'subtitle1',
                color: 'textSecondary',
              }}
              secondaryTypographyProps={{
                variant: 'subtitle1',
                color: 'textPrimary',
              }}
            />
          </a>
        </ListItem>

        <ListItem
          disableGutters
          data-aos="fade-up"
          className={classes.listItem}
        >
          <ListItemAvatar className={classes.listItemAvatar}>
            <Avatar
              src="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-pin.png"
              srcSet="https://assets.maccarianagency.com/the-front/illustrations/contact-icon-pin@2x.png 2x"
              className={classes.icon}
            />
          </ListItemAvatar>
          <ListItemText
            className={classes.listItemText}
            primary="Kancel"
            secondary="Pbram"
            primaryTypographyProps={{
              variant: 'subtitle1',
              color: 'textSecondary',
            }}
            secondaryTypographyProps={{
              variant: 'subtitle1',
              color: 'textPrimary',
            }}
          />
        </ListItem>
      </List>
      <form
        onSubmit={formik.handleSubmit}
        data-netlify="true"
        name="KontaktniFormular"
        method="post"
      >
        <input type="hidden" name="KontaktniFormular" value="contact" />
        <div className={classes.form}>
          <Grid container spacing={isMd ? 4 : 2}>
            <Grid item xs={12} data-aos="fade-up">
              <Typography
                variant="subtitle1"
                color="textPrimary"
                className={classes.inputTitle}
              >
                Jmno
              </Typography>
              <TextField
                placeholder="Vae jmno"
                variant="outlined"
                size="medium"
                fullWidth
                type="text"
                id="name"
                name="name"
                value={formik.values.name}
                onChange={formik.handleChange}
                error={formik.touched.name && Boolean(formik.errors.name)}
                helperText={formik.touched.name && formik.errors.name}
              />
            </Grid>
            <Grid item xs={12} data-aos="fade-up">
              <Typography
                variant="subtitle1"
                color="textPrimary"
                className={classes.inputTitle}
              >
                E-mail
              </Typography>
              <TextField
                placeholder="V Email"
                variant="outlined"
                size="medium"
                name="email"
                fullWidth
                type="email"
                id="email"
                value={formik.values.email}
                onChange={formik.handleChange}
                error={formik.touched.email && Boolean(formik.errors.email)}
                helperText={formik.touched.email && formik.errors.email}
              />
            </Grid>
            <Grid item xs={12} data-aos="fade-up">
              <Typography
                variant="subtitle1"
                color="textPrimary"
                className={classes.inputTitle}
              >
                Zprva
              </Typography>
              <TextField
                placeholder="Napite nm zprvu"
                variant="outlined"
                fullWidth
                multiline
                rows={4}
                id="zprava"
                name="zprava"
                value={formik.values.zprava}
                onChange={formik.handleChange}
                error={formik.touched.zprava && Boolean(formik.errors.zprava)}
                helperText={formik.touched.zprava && formik.errors.zprava}
              />
            </Grid>
            <Grid item container justify="center" xs={12}>
              <Button
                variant="contained"
                type="submit"
                color="primary"
                size="large"
              >
                odeslat
              </Button>
            </Grid>
          </Grid>
        </div>
      </form>
    </div>
  );
};

Contact.propTypes = {
  /**
   * External classes
   */
  className: PropTypes.string,
};

export default Contact;
